import React, { useState } from 'react';
import Add from '@mui/icons-material/Add';
import { TextField, Paper, Button } from '@mui/material';
import type { Todo } from '../../App';

const DEFAULT_TODO = { name: '', description: '' };

const useValidation = (todo: any, validations: any) => {
  const [isEmpty, setIsEmpty] = React.useState(true);
  const [minLengthError, setMinLengthError] = React.useState(false);
  React.useEffect(() => {
    for (const validation in validations) {
      switch (validation) {
        case 'minLength':
          todo.length < validations[validation]
            ? setMinLengthError(true)
            : setMinLengthError(false);
          break;
        case 'isEmpty':
          todo ? setIsEmpty(false) : setIsEmpty(true);
      }
    }
  }, [todo]);
  return {
    isEmpty,
    minLengthError,
  };
};
interface PanelProps {
  onAddTodo: ({ name, description }: Omit<Todo, 'id' | 'checked'>) => void;
}

export const Panel: React.FC<PanelProps> = ({ onAddTodo }) => {
  const [todo, setTodo] = useState(DEFAULT_TODO);

  const onClick = () => {
    if (todo.name !== '' && todo.description !== '') {
      onAddTodo(todo);
      setTodo(DEFAULT_TODO);
    }

    if (todo.name !== '' && todo.description !== '') {
      onAddTodo(todo);
      setTodo(DEFAULT_TODO);
    }
  };

  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { value, name } = e.target;
    setTodo({ ...todo, [name]: value });
  };

  return (
    <Paper
      elevation={3}
      sx={{
        width: '100%',
        padding: '25px 30px',
        borderRadius: 2,
        display: 'flex',
        justifyContent: 'space-between',
        alignContent: 'center',
        gap: 1,
      }}>
      {
        <TextField
          value={todo.name}
          onChange={onChange}
          label="title"
          sx={{ width: '300px' }}
          name="name"
        />
      }

      <TextField
        value={todo.description}
        onChange={onChange}
        label="description"
        sx={{ width: '300px' }}
        name="description"
      />
      <Button onClick={onClick} variant="outlined" startIcon={<Add />}>
        Add
      </Button>
    </Paper>
  );
};
